.BasicPreview {
  background: #fff;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  padding: 1.5rem;
  display: flex;
  justify-content: center;
  flex-direction: column;

  @media screen and (min-width: 750px) {
    height: 200px;
    padding: 2rem;
    overflow: hidden;
  }

  &.is-fullHeight {
    height: 100%;
  }

  &.is-topAlign {
    justify-content: start;
  }
}

.BasicPreview-heading {
  align-self: flex-start;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
  font-size: 18px;
  margin-bottom: 1rem;
  display: inline-block;

  &.is-link {
    text-decoration-line: underline;

    &:hover {
      background: darken(white, 15%);
    }
  }
}

.BasicPreview-content {
  line-height: 1.45;
  font-size: 14px;
}



