.Icon {
  width: 18px;
  height: 18px;
  display: inline-block;

  @supports (display: flex) {
    display: block;
    position: static;
  }

  &.is-small {
    width: 12px;
    height: 12px;
  }

  &.is-large {
    width: 24px;
    height: 24px;
  }
}



